import { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import '../styles/globals.css';

function MyApp({ Component, pageProps }) {
    const [mode, setMode] = useState('light');
    const [isSessionExpired, setIsSessionExpired] = useState(false);
    const router = useRouter();

    // Check session on page load or when route changes
    useEffect(() => {
        const savedMode = localStorage.getItem('mode');
        if (savedMode) {
            setMode(savedMode);
        } else {
            const systemMode = window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light';
            setMode(systemMode);
        }

        const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
        const handleChange = (e) => {
            if (savedMode === 'system') {
                setMode(e.matches ? 'dark' : 'light');
            }
        };
        mediaQuery.addEventListener('change', handleChange);

        return () => {
            mediaQuery.removeEventListener('change', handleChange);
        };
    }, []);

    return (
        <div>
            <UserComponent
                mode={mode}
                isSessionExpired={isSessionExpired}  // Pass isSessionExpired here
                setIsSessionExpired={setIsSessionExpired}
                router={router}
                Component={Component}
                pageProps={pageProps}
            />
            <ToastContainer position="top-right" />
        </div>

    );
}

const UserComponent = ({ mode, Component, pageProps }) => {

    return (
        <div className={mode === 'dark' ? 'dark' : ''}>
            <Component {...pageProps} mode={mode} />
        </div>
    );
};

export default MyApp;
